import { TextareaAutosize } from '@material-ui/core'
import React, { FC, useEffect, useState } from 'react'

import { CustomDispatch, RudolfStore, updateFeedback } from '../RudolfReducer'
import { FeedbackMap, FeedbackNode, SequentNode } from '../typings/Checker'
import { JustificationMap } from '../typings/TreeState'
import { convertToSequent, processFeedback } from '../util/carnapAdapter'
import useJSS from './JSONView_styles'

export const checkSequent = async (
  sequent: SequentNode
): Promise<FeedbackNode> => {
  return new Promise((resolve, reject) => {
    try {
      window.Carnap.checkIchikawaJenkinsSLTableau(
        sequent,
        (result: FeedbackNode) => {
          resolve(result)
        }
      )
    } catch (error) {
      reject(error)
    }
  })
}

const checkTree = async (
  tree: any,
  justifications: JustificationMap
): Promise<{ sequent: SequentNode; feedback: FeedbackMap }> => {
  const sequent = convertToSequent(tree, justifications)
  const feedback: FeedbackNode = await checkSequent(sequent)
  return { sequent, feedback: processFeedback(sequent, feedback) }
}
export const JSONView: FC<RudolfStore & { dispatch: CustomDispatch }> = ({
  tree,
  justifications,
  feedback,
  dispatch,
}) => {
  useEffect(() => {
    if (window.Carnap) {
      checkTree(tree, justifications)
        .then(({ sequent, feedback }) => {
          return dispatch(updateFeedback({ success: true, feedback, sequent }))
        })
        .catch(({ message }: Error) => {
          return dispatch(updateFeedback({ success: false, error: message }))
        })
    }
  }, [dispatch, justifications, tree])
  const classes = useJSS()
  const [open, setOpen] = useState(false)
  return (
    <div className={classes.Bounder}>
      <div
        className={classes.Toggle}
        onClick={() => {
          setOpen(!open)
        }}
      >
        {open ? 'close' : 'open'}
      </div>
      <TextareaAutosize
        className={classes.TextArea}
        value={JSON.stringify({ tree, justifications, feedback })}
        style={{
          maxHeight: open ? '50vh' : '0vh',
        }}
      />
    </div>
  )
}
